* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
}

input {
    color: grey;
    font-size: 1.2rem;
    width: 100%;
}

.card-grid {
    
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

}

.card {
    height:min-content;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p {
    font-size:xx-large;
    padding: 5px;
    text-align:center;

}

.card img {
    width: 100%;
}
